const projects = $(".projects");

let parntersAmount = 0; // счетчик слайдера партнеров

let mainInfoAmount = 0; //счетчик слайдера с главной информацией

let numAmount = 0; //счетчик слайдера цифр

if (projects.length) {
    let mainInfoSlideFirstH = $(
        ".projects__main-info-slide:first-child"
    ).height();

    let parntersSliderFirstH = $(
        ".projects__partners-slide:first-child"
    ).height();

    let numH = $(".projects__pagination-num-item").height();

    $(".projects__main-info").css("max-height", mainInfoSlideFirstH + "px");
    $(".projects__partners").css("max-height", parntersSliderFirstH + "px");
    $(".projects__pagination-num").css("max-height", numH + "px");

    // создание и разбор массива меню
    let anchorsMass = [];

    $(".projects__item").each(function () {
        anchorsMass.push($(this).attr("data-menuanchor"));
    });

    let anchorsStr = anchorsMass.join(", ");

    anchorsStr = anchorsMass.map((el) => "" + el + "");

    //стартовая позиция главного меню

    let startMenuPosition = $(".projects__menu").height() / 2 - 32;
    $(".projects__menu-wrapper").css("top", startMenuPosition + "px");

    let scrollAmount = startMenuPosition;

    //фулпейдж слайдер
    $("#projectsSlides").fullpage({
        licenseKey: "gplv3-license",
        scrollingSpeed: 1100,
        scrollOverflow: true,
        normalScrollElements: ".projects__menu, .projects__menu-container",
        anchors: anchorsStr,
        menu: "#projectsMenu",

        onLeave: function (nextIndex, slideAnchor) {
            let slides = $(".projects__item");
            let activeIndex = nextIndex.index;
            let menu = $(".projects__menu");

            //анимация перелистывания
            slides.removeClass("projects__item--active");
            slides.removeClass("projects__item--first");
            slides.removeClass("projects__item--hide");
            slides.eq(slideAnchor.index).addClass("projects__item--active");
            slides.eq(slideAnchor.index - 1).addClass("projects__item--hide");

            //меню
            menu.find(".projects__menu-item").removeClass(
                "projects__menu-item--active"
            );
            menu.find(".projects__menu-item")
                .eq(slideAnchor.index)
                .addClass("projects__menu-item--active");

            let menuItemActive = menu.find(".projects__menu-item--active");
            let $parent = menuItemActive.parent(),
                elementTop = menuItemActive.offset().top,
                parentTop = $parent.offset().top,
                distance = elementTop - parentTop;

            $(".projects__menu-wrapper").css(
                "top",
                startMenuPosition - distance + 20 + "px"
            );

            scrollAmount = startMenuPosition - distance + 20;

            //слайдер главной информации
            let mainInfoSlideH = $(".projects__main-info-slide")
                .eq(slideAnchor.index)
                .height();

            let infoElements = $(".projects__main-info-slide");
            let infoH = 0;
            for (let i = 0; i < slideAnchor.index; i++) {
                infoH += Math.round(infoElements[i].clientHeight);
            }

            // mainInfoAmount = mainInfoSlideH * slideAnchor.index;

            $(".projects__main-info-wrapper").css("top", -infoH + "px");
            $(".projects__main-info").css("max-height", mainInfoSlideH + "px");

            //слайдер партнеторв
            let parntersSlideH = $(".projects__partners-slide")
                .eq(slideAnchor.index)
                .height();

            let parntersElements = $(".projects__partners-slide");
            let partnersH = 0;
            for (let i = 0; i < slideAnchor.index; i++) {
                partnersH += parntersElements[i].clientHeight;
            }

            $(".projects__partners-wrapper").css("top", -partnersH + "px");
            $(".projects__partners").css("max-height", parntersSlideH + "px");

            //слайдер цифр
            numAmount = numH * slideAnchor.index;
            $(".projects__pagination-num-holder").css("top", -numAmount + "px");
        },
    });

    //открытие меню
    $(".projects__menu-open").on("click", function (e) {
        e.preventDefault();
        let container = $(this).closest(".projects__info-right");
        container.addClass("projects__info-right--active");
        $(".projects__info-container").addClass(
            "projects__info-container--active"
        );
    });

    $(".projects__menu-close").on("click", function (e) {
        e.preventDefault();
        let container = $(this).closest(".projects__info-right");
        container.removeClass("projects__info-right--active");
        $(".projects__info-container").removeClass(
            "projects__info-container--active"
        );
    });

    if ($(window).width() >= 1366) {
        $("body").mousemove(function (e) {
            let target = e.target;
            if (
                target.closest(".header__logo") ||
                target.closest(".header__links") ||
                target.closest(".projects__menu") ||
                target.closest(".projects__menu-btns") ||
                target.closest(".projects__menu-links")
            ) {
                $(".projects__button").hide();
                $(".projects").removeClass("projects--no-cursor");
            } else {
                $(".projects").addClass("projects--no-cursor");
                $(".projects__button").show();
                $(".projects__button").css({
                    left: e.pageX - 70,
                    top: e.pageY - 70,
                });
            }
        });
    }

    let elem = document.querySelector(".projects__menu-group");
    let innerMenu = $(".projects__menu-wrapper");
    let innerMenuH = innerMenu.height();
    let elemH = $(".projects__menu-group").height();

    let diffH = innerMenuH - elemH;

    if (elem.addEventListener) {
        if ("onwheel" in document) {
            // IE9+, FF17+
            elem.addEventListener("wheel", onWheel);
        } else if ("onmousewheel" in document) {
            // устаревший вариант события
            elem.addEventListener("mousewheel", onWheel);
        } else {
            // Firefox < 17
            elem.addEventListener("MozMousePixelScroll", onWheel);
        }
    } else {
        // IE8-
        elem.attachEvent("onmousewheel", onWheel);
    }

    // Это решение предусматривает поддержку IE8-
    function onWheel(e) {
        e = e || window.event;

        // deltaY, detail содержат пиксели
        // wheelDelta не дает возможность узнать количество пикселей
        // onwheel || MozMousePixelScroll || onmousewheel
        var delta = e.deltaY || e.detail || e.wheelDelta;

        if (delta > 0) {
            //down
            scrollAmount += 8;
            // if (scrollAmount <= diffH + 50) {
            innerMenu.css("top", scrollAmount + "px");
            // } else {
            //     scrollAmount = diffH + 50;
            // }
        } else {
            //up

            scrollAmount -= 8;
            // if (scrollAmount >= 0) {
            innerMenu.css("top", scrollAmount + "px");
            // } else {
            //     scrollAmount = 0;
            // }
        }
    }
}
