    if ($('main').hasClass('project5')) {
        $(window).on('scroll', function (e) {
            if ($(window).width() > 1140) {
                var doccs = $(document).scrollTop()
                var decorLine = $('.project5-adaptiv__block')
                var decorLineOff = $('.project5-adaptiv__block').offset().top
                var aboutUs5 = $('.project5-sect__aboutUs')
                var aboutUs5Off = $('.project5-sect__aboutUs').offset().top
                var font5 = $('.project5-sect__font')
                var font5Off = $('.project5-sect__font').offset().top
                var screens5 = $('.project5-sect__screens')
                var screens5Off = $('.project5-sect__screens').offset().top

                if (
                    doccs > decorLineOff - 2000 &&
                    doccs <
                        decorLineOff + $('.project5-adaptiv__block').height()
                ) {
                    var transf = (doccs - decorLineOff) / 30
                    transf < -35 ? (transf = -35) : transf

                    $('.project5-adaptiv__decor-line').css(
                        'transform',
                        'translateY(' + transf * 3 + 'px)'
                    )
                }
                if (
                    doccs > aboutUs5Off - 2000 &&
                    doccs < aboutUs5Off + aboutUs5.height()
                ) {
                    var transf = (doccs - aboutUs5Off) / 30
                    transf < -35 ? (transf = -35) : transf

                    $('.pixel').css(
                        'transform',
                        'translateY(' + transf * 3 + 'px)'
                    )
                }
                if (
                    doccs > font5Off - 2000 &&
                    doccs < font5Off + font5.height()
                ) {
                    var transf = (doccs - font5Off) / 30
                    transf < -35 ? (transf = -35) : transf

                    $('.pixel').css(
                        'transform',
                        'translateY(' + transf * 3 + 'px)'
                    )
                }
                if (
                    doccs > screens5Off - 2000 &&
                    doccs < screens5Off + screens5.height()
                ) {
                    var transf = (doccs - screens5Off) / 30
                    transf < -35 ? (transf = -35) : transf

                    $('.pixel').css(
                        'transform',
                        'translateY(' + transf * 3 + 'px)'
                    )
                }
            } else {
                $('.project5-adaptiv__decor-line').removeAttr('style')
                $('.pixel').css('transform', 'translateY(' + transf * 3 + 'px)')
            }
        })
    }

